import React from "react";
import { Menu } from "semantic-ui-react";
import { Link } from "react-router-dom";

import firebase from "./utils/firebase";

function Header(){
    const [user, setUser] = React.useState(null);
    React.useEffect(() => {
        firebase.auth().onAuthStateChanged((currentUser) => {
            setUser(currentUser)
        });
    }, []);
    return (
        
        <Menu size="large">
            <Menu.Item as={Link} to="/">
                AR Classroom
            </Menu.Item>
            
            <Menu.Menu position="right">
                {user ? (
                <>  
                    <Menu.Item as={Link} to ="/games"></Menu.Item>
                    <Menu.Item as={Link} to ="/join-s"></Menu.Item>
                    <Menu.Item as={Link} to ="/results"></Menu.Item>
                    <Menu.Item as={Link} to ="/settings"></Menu.Item>
                    <Menu.Item onClick={() => firebase.auth().signOut()}></Menu.Item>
                </> 
                ) : (
                    <Menu.Item as={Link} to ="/signin">/</Menu.Item>
                )}
            </Menu.Menu>
            
        </Menu>
    );
    
}

export default Header;